<template>
    <span >
        <slot name="default"></slot>
        <div ref="popupcontent" :style="{'display':'none',width:'auto','position':'absolute','max-width':max_width,'min-width':min_width,'left':popup_left+'px','top':popup_top+'px','visibility':'hidden','opacity':popup_opacity,'transition':'0.5s'}">
            <span class="popper-caret-bg" :style="{width:0,height:0,borderLeft:'9px solid transparent',borderRight:'9px solid transparent',borderBottom:'9px solid '+border_color,'left':(popup_width)-9+'px','position':'absolute','top':'-8px'}"></span>
            <span class="popper-caret" :style="{width:0,height:0,borderLeft:'7px solid transparent',borderRight:'7px solid transparent',borderBottom:'7px solid '+caret_bg_color,'left':(popup_width)-7+'px','position':'absolute','top':'-6px'}"></span>
            <div class="popper-container" :style="{'border-color':border_color,'border-style':'solid',borderWidth:'1px','border-radius':'10px'}">
                <div class="popper-header" :style="{'display':has_header?'block':'none','background-color':header_bg_color,'border-radius':'10px 10px 0px 0px', padding:'5px'}">
                    <slot name="title">{{title}}</slot>
                </div>
                <div class="popper-content" :style="{'background-color':content_bg_color,'padding':'5px','border-radius':'10px'}">
                    <slot name="content">
                        <div v-html="content"></div>
                    </slot>
                </div>
                <div class="popper-footer" :style="{display:has_footer?'block':'none'}">
                </div>
            </div>
            
        </div><!--popupcontent-->
    </span>
</template>
<script>
export default{
    props:{
        title:{
            required:false,
            type:String,
            default:''
        },
        content:{
            required:false,
            type:String,
            default:''
        },
        min_width:{
            required:false,
            type:String,
            default:'auto'
        },
        max_width:{
            required:false,
            type:String,
            default:'250px'
        },
        header_bg_color:{
            required:false,
            type:String,
            default:'#ccc'
        },
        footer_bg_color:{
            required:false,
            type:String,
            default:'#ccc'
        },
        content_bg_color:{
            required:false,
            type:String,
            default:'#fff'
        },
        border_color:{
            required:false,
            type:String,
            default:'#afafaf'
        },
        placement:{
            required:false,
            type:String,
            default:'auto'
        },
        target:{
            required:false,
            type:String,
            default:null
        },
        keep_on_over:{
            required:false,
            type:Boolean,
            default:false
        }
    },
    computed:{
        has_header:function(){
            return this.title!='' || (typeof this.$slots.title!=='undefined' && this.$slots.title.length>0)?true:false;
        },
        has_footer:function(){
            return typeof this.$slots.footer!=='undefined'?true:false;
        },
        caret_bg_color:function(){
            var bg_color = this.content_bg_color;
            if(this.has_header){
                bg_color= this.header_bg_color;
            }
            console.log('caret bg color',bg_color);
            return bg_color;
        }
    },
    data(){
        return {
            dsDom:null, //default slot dom
            popupO:null,
            popup_left:'0px',
            popup_top:'0px',
            popup_opacity:0,
            popup_width:0,
            hide_timeout:-1,
            hide_styles_timeout:-1,
            is_showing: false,
            current_placement:'' //top, top-left, top-right, bottom, bottom-left, bottom-right (default is bottom)
        }
    },
    methods:{
        setupHandlers(){
            if(this.dsDom!=null){
                var me = this;
                if(this.keep_on_over){
                    this.popupO.addEventListener('mouseover',()=>{
                        console.log('on over');
                        clearTimeout(me.hide_styles_timeout);
                        clearTimeout(me.hide_timeout);
                        this.is_showing = true;
                        me.show();
                    })
                    this.popupO.addEventListener('mouseout',()=>{
                        this.is_showing = false;
                        me.hide();
                    })
                }
                this.dsDom.addEventListener('focus',()=>{
                    clearTimeout(me.hide_styles_timeout);
                    clearTimeout(me.hide_timeout)
                    this.is_showing = true;
                    me.show();
                })
                this.dsDom.addEventListener('mouseover',()=>{
                    console.log('mouse over');
                    clearTimeout(me.hide_styles_timeout);
                    clearTimeout(me.hide_timeout);
                    this.is_showing = true;
                    me.show();
                })
                this.dsDom.addEventListener('blur',()=>{
                    this.is_showing = false;
                    me.hide_timeout = setTimeout(()=>{me.hide()},100);
                })
                this.dsDom.addEventListener('mouseout',()=>{
                    this.is_showing = false;
                    console.log('mouse out');
                    me.hide_timeout = setTimeout(()=>{me.hide();},100);
                })
            }
        },
        
        removeHandlers(){

        },
        measureViewport(){
            var div = document.createElement('div');
            div.style.zIndex=-1;
            div.style.position='fixed';
            div.style.visibility='hidden';
            div.style.opacity=0;
            div.style.top='0px';
            div.style.left='0px';
            div.style.right='0px';
            div.style.bottom='0px';
            document.body.appendChild(div);
            var dim = div.getBoundingClientRect();
            var view = {
                w: dim.width,
                h: dim.height
            }
            document.body.removeChild(div);
            return view;
        },
        show(){
            var target_dim = this.target_dim();
            
            //console.log(this.popupO);
            //console.log(this.popupO.style);
            //this.popupO.style.opacity = 0;
            this.popupO.style.visibility = 'visible';
            this.popupO.style.display='inline-block';

            //measure after we set display to inline-block because in hidden it can report wrongly
            var popper_dim = this.popper_dim();

            var view_dim = this.measureViewport();
            //console.log(view_dim);
            var left = target_dim.left+(target_dim.width/2)-(popper_dim.width/2);
            if(left<0){
                left = target_dim.left;
            }
            this.popup_left = left;
            var top = target_dim.top+target_dim.height+10;
            if(top>view_dim.h){
                top = target_dim.top - popper_dim.height-10;
            }
            this.popup_top = top;
            this.popupO.style.visibility = 'visible';
            //this.popupO.style.transition='0.5s';
            this.popup_width = popper_dim.width/2;
            //console.log(popper_dim);
            this.popup_opacity = 1;

        },
        hide(){
            this.popup_opacity = 0;
           
            this.hide_styles_timeout = setTimeout(()=>{
                 this.popupO.style.visibility = 'hidden';
                this.popup_display='none';
            },500);
        },
        target_dim(){
            return this.dsDom.getBoundingClientRect();
        },
        popper_dim(){
            return this.popupO.getBoundingClientRect();
        }
    },
    mounted(){
        //console.log(this.$slots.default[0].elm);
        
        this.popupO = this.$refs['popupcontent']
        document.body.appendChild( this.popupO );

        if(typeof this.$slots.default!=='undefined' && this.$slots.default.length>0){
            this.dsDom = this.$slots.default[0].elm
        }else if(this.target!=null){
            this.dsDom = document.querySelector(this.target);
            this.$el.style.display='none';
        }
        //console.log(this.popupO);
        this.setupHandlers();
    },
    destroy(){
        this.removeHandlers();
    }
}
</script>